import { rfqservice } from './../../rfq-managment/rfq-service';
import { Observable, Subscription } from 'rxjs';
import { Component, OnInit } from "@angular/core";
import { LocalDataSource } from "ng2-smart-table";
import { SmartTableData } from "../../../@core/data/smart-table";
import { HttpClient } from "@angular/common/http";
import {Router, ActivatedRoute} from "@angular/router"
import { NbThemeService, NbDialogService } from '@nebular/theme';

import { Tab1Component } from '../../extra-components/tabs/tabs.component';
import { AuthenticationService } from '../../../auth/Auth.Service';
import { ShowcaseDialogComponent } from '../../modal-overlays/dialog/showcase-dialog/showcase-dialog.component';


@Component({
  selector: 'ngx-smart-table',
  templateUrl: './smart-table.component.html',
  
  styleUrls: ['./consult-study.component.scss'],
})
export class SmartTableComponent implements OnInit {
  selectedRows: any;
  addFormoffre: boolean=true;
  ngOnInit(): void {
   
  }
  starRate = 2;
  heartRate = 4;
  radioGroupValue = 'This is value 2';
  min: Date;
  max: Date;
  themeName = 'default';
  materials:Array<any>;
  themeSubscription: Subscription;
  Settings: Array<any>;


  settings = {
    selectMode: 'multi',
    actions:{
      add: false,
    edit:false,
    delete:false,

custom:[
{
  name:'editroute',title:` <i  class="nb-edit"  [nbPopover]="icon"></i>`
},
{
  name:'deleteroute',title:` <i  class="nb-trash"  [nbPopover]="icon"></i>`
},
],position: 'right'
    },
   
   
    
    columns: {
      customer: {
        title: 'CUSTOMER',
        type: 'string', addable: false,
      },
      partName: {
        title: 'Part name',
        type: 'string', addable: false,
      },
      partNumber: {
        title: 'Part number',
        type: 'string', addable: false,
      },
      drawningNumber: {
        title: 'Drawning number',
        type: 'string', addable: false,
      },
     
     
    },
  };
tab:number;
  sources: [any]=[0];

    constructor(private http:HttpClient,private route: ActivatedRoute,private authserv:AuthenticationService,private rfqser:rfqservice,private router: Router,private themeService: NbThemeService,private dialogService:NbDialogService) {
      if (this.authserv.Role['subject']=='assistante') { 
        this.dialogService.open(ShowcaseDialogComponent, {
          context: {
            title: 'User not permetted',
            message:'this section is not a part of your role !!',
          },
        });
        this.router.navigate(['pages']);
      }
      else{
      this.sources.pop();
      this.http.get<any>('http://localhost:3000/rfq').subscribe(result=>{
  this.tab=result.length;
  var i=0;
  for(i;i<(result.length);i++)
  {
  this.http.get<any>('http://localhost:3000/rfq/'+result[i]+'/studies').subscribe(result=>{
  if (result!=0)
  this.sources.push(result);
  
  });    }
     });
       

    this.themeSubscription = this.themeService.getJsTheme().subscribe(theme => {
      this.themeName = theme.name;
      this.init(theme.variables);
      
    });}

  }
 
editStudy(event){
  switch ( event.action) {
    case 'deleteroute':
    {  if (this.authserv.Role['subject']=='admin'){
      this.http.get<any>('http://localhost:3000/rfq/oh/'+event.data._id).subscribe(result=>{
this.http.delete<any> ('http://localhost:3000/rfq/'+result.relatedRFQ+'/'+event.data._id).subscribe(result=>{
  this.sources=[0];      this.sources.pop();

this.http.get<any>('http://localhost:3000/rfq').subscribe(result=>{
this.tab=result.length;
var i=0;
for(i;i<(result.length);i++)
{
this.http.get<any>('http://localhost:3000/rfq/'+result[i]+'/studies').subscribe(result=>{
if (result!=0)
this.sources.push(result);

});    }
});
 }); });
 this.http.delete<any>('http://localhost:3000/rfq/rfqid/'+event.data._id+'/materials').subscribe(res=>{});
this.http.delete<any>('http://localhost:3000/rfq/rfqid/'+event.data._id+'/packages').subscribe(res=>{});
this.http.delete<any>('http://localhost:3000/rfq/rfqid/'+event.data._id+'/processes').subscribe(res=>{});
}else { 
  this.dialogService.open(ShowcaseDialogComponent, {
    context: {
      title: 'User not permetted',
      message:'this section is not a part of your role !!',
    },
  });
 
}
}
    break;
    case 'editroute':
        if ((this.authserv.Role['subject']=='admin')||(this.authserv.Role['subject']=='technicien') ){
   this.router.navigate(['pages/study-managment-elements/editStudy',event.data._id]);}
   else{ 
    this.dialogService.open(ShowcaseDialogComponent, {
      context: {
        title: 'User not permetted',
        message:'this section is not a part of your role !!',
      },
    });
    
  }
  }}
  addStudy(){
    if ((this.authserv.Role['subject']!='admin')&&(this.authserv.Role['subject']!='technicien')){ 
      this.dialogService.open(ShowcaseDialogComponent, {
        context: {
          title: 'User not permetted',
          message:'this section is not a part of your role !!',
        },
      });
      
    }
     else{this.router.navigate(['pages/study-managment-elements/addStudy']);}
    }
    onRowSelect(event) {
      this.selectedRows = event.selected;
      this.http.get<any>('http://localhost:3000/rfq/oh/'+this.selectedRows[0]._id).subscribe(result=>{
        this.rfqser.idrfq=result.relatedRFQ;
      });
    }
    AddOffreForm(){
      if ((this.authserv.Role['subject']!='admin')){ 
        this.dialogService.open(ShowcaseDialogComponent, {
          context: {
            title: 'User not permetted',
            message:'this section is not a part of your role !!',
          },
        });
       
      }else{
        if (this.selectedRows==null) {
          this.dialogService.open(ShowcaseDialogComponent, {
            context: {
              title: 'Study not Selected',
              message:'TO create an Offer you should select one or many Studies',
            },
          });
        }
          
        
      else{
      this.addFormoffre=false;}
    }}
    cancelOfferform(){
      this.addFormoffre=true;
    }
    AddOffre(OfferData){
    
this.addFormoffre=true;
if(this.selectedRows!=null){
     if(this.rfqser.idoffre==null) {this.http.post<any>('http://localhost:3000/rfq/offre/addoffre/'+this.rfqser.idrfq+'/'+this.selectedRows[0]._id,OfferData).subscribe(result=>{
this.rfqser.idoffre=result['_id'];
for(var i=1;i<(this.selectedRows.length);i++){
  this.http.post<any>('http://localhost:3000/rfq/'+this.rfqser.idoffre+'/offre/'+this.selectedRows[i]._id,null).subscribe(result=>{});
}
this.router.navigate(['pages/offer-managment-elements/smart-table']);
this.rfqser.idoffre=null;
});

};

}
    }
  init(colors: any) {
    this.Settings = [
      {
        status: 'primary',
        container: 'primary-container',
        title: 'Primary Button',
        buttonTitle: 'Add Study',
        default: {
          gradientLeft: `adjust-hue(${colors.primary}, 20deg)`,
          gradientRight: colors.primary,
        },
        corporate: {
          color: colors.primary,
          glow: {
            params: '0 0 20px 0',
            color: 'rgba (115, 161, 255, 0.5)',
          },
        },
        cosmic: {
          gradientLeft: `adjust-hue(${colors.primary}, 20deg)`,
          gradientRight: colors.primary,
          bevel: `shade(${colors.primary}, 14%)`,
          shadow: 'rgba (6, 7, 64, 0.5)',
          glow: {
            params: '0 2px 12px 0',
            color: `adjust-hue(${colors.primary}, 10deg)`,
          },
        },
      },
   
    ];
  }
}

