  onChange(event)
  {
    this.MaterialCost=Math.round((this.Transport+(this.Grossweight*this.Rate)/(1-this.Scrap)*(1+this.Handling))*10000000)/10000000
  this.TotalMaterial=this.MaterialCost;
}
onChangeWeight(event)
{
  this.Grossweight=Math.round(((2.5/1000)+this.Netweight)*10000000)/10000000;
this.Transport=Math.round((0.12*this.Netweight)*10000000)/10000000;
this.MaterialCost=Math.round((this.Transport+(this.Grossweight*this.Rate)/(1-this.Scrap)*(1+this.Handling))*10000000)/10000000
this.TotalMaterial=this.MaterialCost;
}
onChangeRate(event)
{
 
  this.MaterialCost=Math.round((this.Transport+(this.Grossweight*this.Rate)/(1-this.Scrap)*(1+this.Handling))*10000000)/10000000
  this.TotalMaterial=this.MaterialCost;
}
onChangeScrap(event){
  this.MaterialCost=Math.round((this.Transport+(this.Grossweight*this.Rate)/(1-this.Scrap)*(1+this.Handling))*10000000)/10000000
  this.TotalMaterial=this.MaterialCost;
}
UCCartonevent(event){
if(this.UCCarton=="Carton cage 565X380X370"){this.PackagingCost=0.9713}
else if(this.UCCarton=="Carton Cage NSK"){this.PackagingCost=1.394}
else if(this.UCCarton=="Galia A09 600X400X300"){this.PackagingCost=0.766}
}


