  materialEdit(event){
    switch ( event.action) {
      case 'deleteMaterial':
      {this.http.delete<any>('http://localhost:3000/rfq/material/'+event.data._id+'/'+this.rfqser.idstudy).subscribe(res=>{
        this.http.get<any>('http://localhost:3000/rfq/rfqid/'+this.rfqser.idstudy+'/materials').subscribe(res=>{
      
          this.sourceMaterial=new LocalDataSource(res)
            });
      });
    

      }
      break;
   case 'editMaterial':{
    this.cancelEditContent();
    this.rfqser.materialid=event.data._id;
    this.http.get<any>('http://localhost:3000/rfq/material/'+event.data._id+'/'+this.rfqser.idstudy).subscribe(res=>{  
      this.MaterialDescription=res.MaterialDescription; 
      this.Netweight=res.Netweight;
      this.Grossweight=res.Grossweight;
      this.Rate=res.Rate;
      this.Transport=res.Transport;
      this.Scrap=res.Scrap;
      this.Handling=res.Handling;
      this.MaterialCost=res.MaterialCost;
      this.TotalMaterial=res.TotalMaterial;
     });

    
    
  }
}
}
  
  packageEdit(event){
    switch ( event.action) {
      case 'deletePackage':
      {this.http.delete<any>('http://localhost:3000/rfq/package/'+event.data._id+'/'+this.rfqser.idstudy).subscribe(res=>{
        this.http.get<any>('http://localhost:3000/rfq/rfqid/'+this.rfqser.idstudy+'/packages').subscribe(res=>{
      
          this.sourcePackage=new LocalDataSource(res)
            });
      });
    

      }
      break;
      case 'editPackage':{
        this.cancelEditPackage();
        this.rfqser.packageid=event.data._id;
        this.http.get<any>('http://localhost:3000/rfq/package/'+event.data._id+'/'+this.rfqser.idstudy).subscribe(res=>{  
          this.UCCarton=res.UCCarton;
          this.Palette=res.Palette;

          
          this.Intercalary=res.Intercalary;
          this.Bag=res.Bag;
          this.NbrepieceparUC=res.NbrepieceparUC;
          this.NbreUCparpalette=res.NbreUCparpalette;
          this.NbreprodparUM=res.NbreprodparUM;
          this.PackagingCost=res.PackagingCost;
         });
    
        
        
      }
    }

    }
    
  
    processEdit(event){
    switch ( event.action) {
      case 'deleteprocess':
      {this.http.delete<any>('http://localhost:3000/rfq/process/'+event.data._id+'/'+this.rfqser.idstudy).subscribe(res=>{
        this.http.get<any>('http://localhost:3000/rfq/rfqid/'+this.rfqser.idstudy+'/processes').subscribe(res=>{
      
          this.sourceProcess=new LocalDataSource(res)
            });
      });
    

      }
      break;
      case 'editprocess':
      {
        this.cancelEditProcess();
        this.rfqser.processid=event.data._id;
        this.http.get<any>('http://localhost:3000/rfq/process/'+event.data._id+'/'+this.rfqser.idstudy).subscribe(res=>{  
         
          this.Operation=res.Operation;
          this.FMN=res.FMN;
          this.MachineType=res.MachineType;
          this.MachineRate=res.MachineRate;
          this.PartCycle=res.PartCycle;
          this.CycleTime=res.CycleTime;
          this.PartperHour=res.PartperHour;
          this.NbreHead=res.NbreHead;
          this.PrcocessScrap=res.PrcocessScrap;
          this.DirectLaborRate=res.DirectLaborRate;
          this.MachineCost=res.MachineCost;
          this.LaborCost=res.LaborCost;
          this.PartPerAssy=res.PartPerAssy;
          this.Coeficient=res.Coeficient;
          this.ProdMinQte=res.ProdMinQte;
          this.MachineOccupancy=res.MachineOccupancy;
          this.Cost=res.Cost;
          this.StartingCost=res.StartingCost;
          this.Totalprocess=res.Totalprocess;
        
         
         
         
  
         
          
         });

      }
    }
  }
