





import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';

import { StudyManagmentComponent } from './study-managment.component';
import { SmartTableComponent } from '../study-managment/consult-study/smart-table.component';
import { AddStudyComponent } from './add-study/add-study.component';
import { EditStudyComponent } from './edit-study/edit-study.component';
import { AuthGuard } from '../../auth/auth.gards';


const routes: Routes = [{
  path: '',
  component: StudyManagmentComponent,
  children: [
    {
      path: 'addStudy',
      component: AddStudyComponent,canActivate: [AuthGuard]
    },
    {
      path: 'editStudy/:id',
      component: EditStudyComponent,canActivate: [AuthGuard]
    },
   
   
    {
      path: 'smart-table',
      component: SmartTableComponent,canActivate: [AuthGuard]    },
  ],
}];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
  ],
  exports: [
    RouterModule
  ],
})
export class StudyManagmentRoutingModule {

}

export const routedComponents = [
  StudyManagmentComponent,
  AddStudyComponent,
  EditStudyComponent,
  SmartTableComponent
];
